<template>
  <div class="loading" v-if="this.$store.state.loading == true">
    <div class="spinner-grow" style="width: 3rem; height: 3rem" role="status">
      <span class="visually-hidden">Loading...</span>
    </div>
  </div>
  <div v-if="!this.$store.state.loading == true">
    <MyBreadcrumb />
    <!--single property-->
    <section class="single-property-page" v-if="single_blog">
      <div class="container">
        <div class="property-content">
          <div class="row">
            <div class="col-md-9 col-lg-9">
              <!--images-->
              <div class="single-property-box">
                <div class="single-property-images">
                  <div class="slider-for">
                    <div class="item">
                      <!-- عرض الصورة يبقى ضعف الطول، عشان الصورة تظهر حلوة بس مش أكتر--><a
                        data-fancybox="demo"
                        :src="single_blog.main_image"
                        data-caption=""
                        ><img
                          class="img-fluid"
                          :src="single_blog.main_image"
                          :alt="single_blog.title"
                      /></a>
                    </div>
                  </div>
                </div>

                <!--details-->
                <div class="single-property-text">
                  <div class="subtitle">
                    <h3>{{single_blog.title}}</h3>
                    <span class="line"></span>
                  </div>
                  <p v-html="single_blog.description"></p>
                </div>

                <!--video-->
                <div class="single-property-video">
                  <div class="subtitle">
                    <h3>فيديو</h3>
                    <span class="line"></span>
                  </div>
                  <div class="video">
                    <div class="ratio ratio-16x9">
                      <iframe
                        :src="single_blog.video"
                        title="YouTube video"
                        allowfullscreen
                      ></iframe>
                    </div>
                    <!-- في حالة لو الفيديو مرفوع سيرفر مش يوتيوب-->
                  </div>
                </div>
              </div>
            </div>
            <!--aside area-->
            <div class="col-md-3 col-lg-3">
              <aside>
                <div class="share-property">
                  <div class="subtitle">
                    <h3>مشاركة الآن</h3>
                  </div>
                  <ul>
                    <li>
                      <ShareNetwork
                        network="facebook"
                        :url="url"
                        :title="single_blog.title"
                        :description="single_blog.description"
                        quote="The hot reload is so fast it\'s near instant. - Evan You"
                        hashtags="vuejs,vite"
                      >
                        <i class="bi bi-facebook"> </i>
                      </ShareNetwork>
                    </li>
                    <li>
                      <ShareNetwork
                        network="twitter"
                        :url="url"
                        :title="single_blog.title"
                        hashtags="vuejs,vite"
                      >
                        <i class="bi bi-twitter"> </i>
                      </ShareNetwork>
                    </li>
                    <li>
                      <ShareNetwork
                        network="WhatsApp"
                        :url="url"
                        :title="single_blog.title"
                        :description="single_blog.description"
                      >
                        <i class="bi bi-whatsapp"></i>
                      </ShareNetwork>
                    </li>
                  </ul>
                </div>

                <div class="related-properties">
                  <div class="subtitle">
                    <h3>مواضيع ذات صلة</h3>
                  </div>
                  <div class="row" v-if="single_blog.related">
                    <div
                      class="col-sm-12 col-md-12 col-lg-12"
                      v-for="related in single_blog.related"
                      :key="related.id"
                    >
                      <div class="property-box">
                        <div class="property-image">
                          <img :src="related.image" :alt="related.title" />
                        </div>
                        <div class="property-details">
                          <router-link :to="`/blog/${related.id}`">
                            <h2 class="title">
                              {{ related.title }}
                            </h2>
                          </router-link>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </aside>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!--single property-->
  </div>
</template>

<script>
// @ is an alias to /src
import MyBreadcrumb from "@/components/global/MyBreadcrumb.vue";
import axios from "axios";

export default {
  name: "SingleBlogView",
  components: {
    MyBreadcrumb,
  },
  data() {
    return {
      single_blog: null,
    };
  },
  methods: {
    async initData() {
      this.$store.state.loading = true;
      await axios
        .get(`singlePropety?id=${this.$route.params.id}`, {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((result) => {
          this.single_blog = result.data.data;
          this.$store.state.loading = false;
        });
    },
  },
  async created() {
    this.initData();
    this.$watch(
      () => this.$route.params,
      async () => {
        this.initData();
      }
    );
  },
};
</script>