<template>
  <meta charset="UTF-8" />
  <meta name="description" content="description here" />
  <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
  <meta name="keywords" content="" />
  <meta name="viewport" content="width=device-width, initial-scale=1.0" />

  <MyHeader :headerData="settingsData"/>

  <router-view />

  <MyFooter :footerData="settingsData"/>
</template>


<script>
import MyHeader from "@/components/global/MyHeader.vue";
import MyFooter from "@/components/global/MyFooter.vue";
import { mapState } from "vuex";

export default {
  components: {
    MyHeader,
    MyFooter,
  },

  data() {
    return {
      isLoaded: false,
      headerData: [],
      footerData: [],
      isHome: null,
      showToTopBtn: null,
      headerFixedVal: null,
    };
  },

  computed: {
    ...mapState(["settingsData"]),
  },
  created() {
    this.$store.dispatch("getSettingsData");
  },
  watch: {
    $route: {
      immediate: true,
      handler(to) {
        if (to.name == "HomeView") {
          console.log("ddd");
          this.isHome = true;
        } else {
          this.isHome = null;
        }
      },
    },
  },
};
</script>
