<template>
  <!-- about-->
  <div class="loading" v-if="this.$store.state.loading == true">
    <div class="spinner-grow" style="width: 3rem; height: 3rem" role="status">
      <span class="visually-hidden">Loading...</span>
    </div>
  </div>
  <div v-if="!this.$store.state.loading == true">
    <MyBreadcrumb />

    <div class="map">
      <iframe
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d110550.39964813001!2d31.11960275764731!3d30.01676975648693!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x1458469235579697%3A0x4e91d61f9878fc52!2sGiza%2C%20El%20Omraniya%2C%20Giza%20Governorate!5e0!3m2!1sen!2seg!4v1630871023803!5m2!1sen!2seg"
        width="100%"
        height="450"
        style="border: 0"
        allowfullscreen=""
        loading="lazy"
      ></iframe>
    </div>
    <!-- contact page map-->
    <!-- contact-->
    <section class="contact" v-if="contactData">
      <div class="container">
        <div class="info-area">
          <div class="row">
            <div class="col-md-4 col-lg-4">
              <div class="info-box">
                <i class="bi bi-envelope"> </i>
                <h4>الإيميل</h4>
                <a :href="`mailto:${contactData.data.email}`">{{
                  contactData.data.email
                }}</a>
              </div>
            </div>
            <div class="col-md-4 col-lg-4">
              <div class="info-box">
                <i class="bi bi-telephone"></i>
                <h4>الهاتف</h4>
                <a :href="`tel:${contactData.data.phone}`">
                  {{ contactData.data.phone }}
                </a>
              </div>
            </div>
            <div class="col-md-4 col-lg-4">
              <div class="info-box">
                <i class="bi bi-pin-map"></i>
                <h4>الموقع</h4>
                <span> {{ contactData.data.address }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="form-area">
          <div class="main-title">
            <h2>تواصل معنا الان<span class="line"></span></h2>
          </div>
          <div class="contact-box">
            <div class="form-content">
              <form
                class="g-3 needs-validation"
                action
                @submit.prevent="handleContactForm"
              >
                <div class="row">
                  <div class="col-lg-12">
                    <div class="form-group position-relative">
                      <input
                        class="form-control"
                        type="text"
                        placeholder="الاسم"
                        v-model="contactFormData.name"
                        v-on:keyup="namemonitor"
                      />
                      <div class="invalid-feedback d-block" id="name_error" v-if="contactErrors.name">
                        {{ contactErrors.name }}
                      </div>
                    </div>
                  </div>
                  <div class="col-lg-12">
                    <div class="form-group position-relative">
                      <input
                        class="form-control"
                        type="number"
                        placeholder="الهاتف"
                        v-model="contactFormData.phone"
                      />
                      <div class="invalid-feedback d-block" id="name_error">
                        {{ contactErrors.phone }}
                      </div>
                    </div>
                  </div>
                  <div class="col-lg-12">
                    <div class="form-group position-relative">
                      <div class="input-group has-validation">
                        <input
                          class="form-control"
                          type="email"
                          placeholder="الإيميل"
                          v-model="contactFormData.email"
                        />
                        <div class="invalid-feedback d-block" id="name_error">
                          {{ contactErrors.email }}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-lg-12">
                    <div class="form-group position-relative">
                      <textarea
                        class="form-control"
                        rows="6"
                        v-model="contactFormData.message"
                        placeholder="اكتب الرسالة"
                      ></textarea>
                      <div class="invalid-feedback d-block" id="name_error">
                        {{ contactErrors.message }}
                      </div>
                    </div>
                  </div>
                  <div class="col-lg-12 text-center">
                    <div class="form-group position-relative">
                      <button
                        class="btn btn-danger hvr-sweep-to-top"
                        type="submit"
                      >
                        إرسال
                      </button>
                    </div>
                  </div>
                  <div class="col-lg-12" v-if="sent">
                    <div class="text-center">
                      <p class="successfully">تم إرسال الرسالة بنجاح</p>
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
// @ is an alias to /src
import MyBreadcrumb from "@/components/global/MyBreadcrumb.vue";
import { mapState, mapActions } from "vuex";

export default {
  name: "ContactView",
  data() {
    return {
      sent: false,
      contactFormData: {
        name: null,
        phone: null,
        email: null,
        message: null,
      },
      contactErrors: {
        name: null,
        email: null,
        phone: null,
        message: null,
      },
    };
  },
  components: {
    MyBreadcrumb,
  },
  methods: {
    ...mapActions(["storeFormData"]),
    handleContactForm(e) {
      this.sending = true;
      this.sent = null;
      this.storeFormData(this.contactFormData).then(() => {
        if (!this.$store.state.contactErrorsApi) {
          e.target.reset();
          this.sent = true;
          this.contactFormData.name = null;
          this.contactFormData.email = null;
          this.contactFormData.phone = null;
          this.contactFormData.message = null;
          this.contactErrors.name = null;
          this.contactErrors.email = null;
          this.contactErrors.phone = null;
          this.contactErrors.message = null;
        } else {
          this.sent = null;
          let errors = this.$store.state.contactErrorsApi.data.data;
          this.contactErrors.name = errors.name[0];
          this.contactErrors.email = errors.email[0];
          this.contactErrors.phone = errors.phone[0];
          this.contactErrors.message = errors.message[0];
        }
      });
    },
  },
  computed: {
    ...mapState(["contactData"]),
  },
  created() {
    this.$store.dispatch("getContactData");
  },
};
</script>
